// pages/tags/[tag].tsx
import { GetStaticPaths, GetStaticProps } from "next";
import { getAllPosts, Post } from "../../lib/posts";
import BlogList from "../../components/BlogList";
import Header from "../../components/Header";
import { tagMap } from "../../lib/tags";
import SEO from "../../components/SEO";

interface TagPageProps {
  tag: string;
  posts: Post[];
}

export default function TagPage({ tag, posts }: TagPageProps) {
  return (
    <>
      <SEO title="TAG - KuromiPiPi" />
      <div
        className="container"
        style={{ maxWidth: "640px", margin: "0 auto" }}
      >
        <Header
          buttons={[
            // { label: "HOME", href: "/" },
            { label: "POSTS", href: "/posts" },
            // { label: "COZYDIARY", href: "/cozydiary" },
            // { label: "MOMENTS", href: "/moments" },
            { label: "TAGS", href: "/tags" },
            // { label: "NOW", href: "/now" },
          ]}
        />
        <main style={{ marginTop: "2rem" }}>
          <h1>Tag: {tagMap[tag] || tag}</h1>
          <BlogList items={posts} />
        </main>
      </div>
    </>
  );
}

export const getStaticPaths: GetStaticPaths = async () => {
  const posts = getAllPosts();
  const tags = Array.from(new Set(posts.flatMap((p) => p.tags)));
  const paths = tags.map((tag) => ({ params: { tag } }));

  return { paths, fallback: false };
};

export const getStaticProps: GetStaticProps = async ({ params }) => {
  const tag = params?.tag as string;
  //   Áõ¥Êé•Ë∞ÉÁî® getAllPosts(tag) Ëé∑ÂèñÊüê‰∏™ tag ÁöÑÊñáÁ´†
  const posts = getAllPosts(tag);

  return {
    props: { tag, posts },
  };
};
